<template>
  <div class="app">
    <h1 class="app-title"> Client Contacts</h1>

    <div class="search-container">
      <input type="text" v-model="searchTerm" placeholder="Search contacts">
    </div>

    <table class="contact-table">
      <thead>
      <tr>
        <th>Name</th>
        <th>Email</th>
        <th>Phone</th>
        <th>Address</th>
        <th>Company</th>
        <th>Website</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="contact in filteredContacts" :key="contact.id">
        <td>{{ contact.name }}</td>
        <td>{{ contact.email }}</td>
        <td>{{ contact.phone }}</td>
        <td>{{ contact.address.city }}, {{ contact.address.street }}, {{ contact.address.suite }}, {{ contact.address.zipcode }}</td>
        <td>{{ contact.company.name }}</td>
        <td>{{ contact.website }}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'App',
  data() {
    return {
      contacts: [],
      searchTerm: '',
    };
  },
  mounted() {
    this.fetchContacts();
  },
  methods: {
    fetchContacts() {

      axios.get('https://jsonplaceholder.typicode.com/users')
          .then(response => {
            this.contacts = response.data;
          })
          .catch(error => {
            console.error('Error fetching contacts:', error);
          });
    },
  },

  computed: {
    filteredContacts() {
      const term = this.searchTerm.toLowerCase();
      return this.contacts.filter(contact => {
        return (
            contact.name.toLowerCase().includes(term) ||
            contact.email.toLowerCase().includes(term) ||
            contact.phone.toLowerCase().includes(term)
        );
      });
    },
  },


};
</script>

<style>
/* Existing styles */

.contact-table {
  width: 100%;
  border-collapse: collapse;
}

.contact-table th,
.contact-table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.contact-table th {
  font-weight: bold;
}

.contact-table tbody tr:last-child {
  border-bottom: none;
}

</style>